:root{
    --height: 94.6vh;
    --main-theme-color: rgb(180, 0, 204);
    --main-theme-color-hover: rgb(119, 0, 135);
    --font: 'Poppins',sans-serif;
}
.navigation{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 50px;
    background: linear-gradient(#202020,#151515);
    font-family: var(--font);
    margin-bottom: 1px;
}
.web-nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.mobile-nav{
    display: none;
}
#logo {
    color: whitesmoke;
    font-size: 1.5rem;
    margin-left: 30px;
    text-decoration: none;
}

.nav-links{
    display: flex;
    justify-content: space-between;
}
#option{
    text-decoration: none;
    font-size: 1.05rem;
    margin: 8px;
    color: whitesmoke;
    transition-duration: 0.5s;
}
#option:hover{
    color: var(--main-theme-color);
    transition-duration: 0.5s;
}
#login-btn{
    font-family: var(--font);
    font-size: 1rem;
    border: none;
    color: whitesmoke;
    margin-right: 30px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    background-color: var(--main-theme-color);
    border-radius: 3px;
    transition-duration: 0.5s;
    text-decoration: none;
}
#login-btn:hover{
    background-color: var(--main-theme-color-hover);
    transition-duration: 0.5s;
}
#logout-btn{
    font-family: var(--font);
    font-size: 1rem;
    border: none;
    color: whitesmoke;
    margin-right: 30px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    background-color: darkred;
    border-radius: 3px;
    transition-duration: 0.5s;
    text-decoration: none;
}
#logout-btn:hover{
    background-color: rgb(110, 2, 2);
    transition-duration: 0.5s;
}
/* For mobile */
#login-btn-m{
    font-family: var(--font);
    font-size: 1.4rem;
    border: none;
    color: whitesmoke;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-top: 15px;
    margin-bottom: 15px;
    background-color: var(--main-theme-color);
    border-radius: 3px;
    transition-duration: 0.5s;
    text-decoration: none;
}
#logout-btn-m{
    font-family: var(--font);
    font-size: 1.4rem;
    border: none;
    color: whitesmoke;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-top: 15px;
    margin-bottom: 15px;
    background-color: darkred;
    border-radius: 3px;
    transition-duration: 0.5s;
    text-decoration: none;
}
#m-option{
    text-decoration: none;
    font-size: 1.5rem;
    margin: 15px;
    color: whitesmoke;
    transition-duration: 0.5s;
}

.hamburger-menu{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
}
.hamburger-menu:hover{
    cursor: pointer;
}
@media only screen and (max-width: 720px) {

    .web-nav{
        display: none;
    }
    .btn-container{
        display: none;
    }
    .mobile-nav{
        appearance: auto;
        display: flex;
        align-items: center;
    }
}
.menu{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.nav-menu {
    display: none;
}
.nav-menu.active{
    position:absolute;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50px;
    width: 100%;
    height: 94.6vh;
    text-decoration: none;
    background: linear-gradient(#161616,#151515);
}