.game-display-page{
    margin: 1.9rem;
}
.content-page{
    margin-left: 15px;
    margin-right: 15px;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
}
.card{
    width: 245px;
    height: 400px;
    background-image: url("../images/wotlk-arthas.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: whitesmoke;
    margin: 7px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: all .2s ease-in-out;
    font-family: 'Poppins',sans-serif;
    border: 1px solid black;
}
.card:hover{
    transform: scale(1.02);
    cursor: pointer;
}
.card-info{
    display: flex;
    flex-direction: column;
}
#card-title{
    text-align: center;
}
#card-description{
    position: relative;
    text-align: center;
    top: 200px;
}
#card-item-price{
   position: relative;
   text-align: center;
   top: 220px;
}
#card-item-developer{
   position: relative;
   text-align: center;
   top: 190px;
}



/* Mobile */


@media only screen and (max-width: 720px) {

    .game-display-page{
       
    }
    .content-page{
        display: flex;
        align-items: center;
        justify-content: center;
    }
}