:root{
    --height: 94.6vh;
    --main-theme-color: rgb(180, 0, 204);
    --main-theme-color-hover: rgb(119, 0, 135);
    --font: 'Poppins',sans-serif;
}
.content{
    font-family: var(--font);
    margin-top: 5px;
    width: 100%;
    background-color: #151515;
}
.page-header{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.welcome-msg{
    color: whitesmoke;
    font-size: 2.5rem;
    text-align: center;
}
.welcome-msg #app-name{
    color: var(--main-theme-color);
}
.description{
    color: whitesmoke;
    text-align: center;
    margin-bottom: 10px;
}
